<template>
  <div class="hello">
    <div v-html="projectPage" />
    <br>
  </div>
</template>

<script>
export default {
  data() {
    return {
      projectPage: ""
    };
  },
  mounted() {
    window.scroll(0, 0);
  },
  updated() {
    window.scroll(0, 0);
  },
  created() {
    this.getMarkdown();
  },
  watch: {
    $route: function(val) {
      this.getMarkdown();
    }
  },
  methods: {
    getMarkdown: function() {
      try {
        this.projectPage = require("@/assets/markdown/project" +
          this.$route.params.id +
          ".md");
      } catch (e) {
        this.projectPage =
          "这篇文章未上传，请联系：<a href='mailto:sharealex@163.com'>sharealex@163.com</a>";
      }
    }
  }
};
</script>


